.App {
padding: 20px;
}

.title-wrapper {
    text-align: center;
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 10px;
    margin-bottom: 20px;
}
.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.box  {
    font-size: 20px;
    font-weight: 500;
    width: 150px;
    height: 150px;
    border: 1px dashed #f1f1f1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    box-sizing: border-box;
    cursor: pointer;
}
.btn {
    color: #666666;
    cursor: pointer;
    width: 80px;
    padding: 5px 20px;
    text-align: center;
    background: beige;
    border-radius: 2px;
    margin: 0 auto 20px;

}
.index,.total {
    width: 100%;
    text-align: center;
}
.index {
    font-size: 24px;
}
.getRed {
    background: rgba(255,0,0,0.5);
}
.getGreen {
    background: rgba(50,205,50,0.5);
}
.result {
    text-align: center;
    font-size: 18px;
    margin-top: 20px;
}
